@font-face {
  font-display: swap;
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url("../../fonts/inter-v13-latin-regular.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  src: url("../../fonts/inter-v13-latin-500.woff2") format("woff2");
}
@font-face {
  font-display: swap;
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  src: url("../../fonts/inter-v13-latin-600.woff2") format("woff2");
}
:root {
  --color-primary-dark: rgba(5, 5, 5, 0.8);
  --color-primary-light: #f8f8f8;
  --color-accent: #9bb537;
  --color-secondary-dark: #050505;
  --color-secondary-lighter: rgba(5, 5, 5, 0.5);
  --color-secondary-light: rgba(5, 5, 5, 0.3);
  --color-secondary-lightest: rgba(5, 5, 5, 0.18);
  --color-third-light: rgba(248, 248, 248, 0.6);
  --color-third-lighter: rgba(248, 248, 248, 0.2);
  --color-star: rgba(5, 5, 5, 0.1);
  --color-star-active: #eea10c;
  --color-card-title: var(--color-primary-light);
  --color-card-text: var(--color-primary-light);
  --color-card-rating: var(--color-primary-light);
  --color-card-heart: var(--color-primary-light);
  --color-modal-add: var(--color-primary-light);
  --color-event-back: var(--color-secondary-dark);
  --display-logo-light: block;
  --display-logo-dark: none;
}

:root[theme=dark] {
  --color-primary-dark: rgba(255, 255, 255, 0.8);
  --color-primary-light: #161616;
  --color-accent: #9bb537;
  --color-secondary-dark: #ffffff;
  --color-secondary-lighter: rgba(255, 255, 255, 0.5);
  --color-secondary-light: rgba(255, 255, 255, 0.3);
  --color-secondary-lightest: rgba(255, 255, 255, 0.18);
  --color-third-light: rgba(248, 248, 248, 0.6);
  --color-third-lighter: rgba(248, 248, 248, 0.2);
  --color-star: rgba(255, 255, 255, 0.18);
  --color-star-active: #eea10c;
  --color-card-title: var(--color-secondary-dark);
  --color-card-text: var(--color-primary-dark);
  --color-card-rating: var(--color-secondary-lighter);
  --color-card-heart: var(--color-secondary-dark);
  --color-modal-add: var(--color-secondary-dark);
  --color-event-back: #050505;
  --display-logo-light: none;
  --display-logo-dark: block;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  color: var(--color-primary-dark);
  background-color: var(--color-primary-light);
  font-family: "Inter", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

a {
  display: inline-block;
  text-decoration: none;
}

address {
  font-style: normal;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.no-scroll {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 20px;
}
@media screen and (min-width: 768px) {
  .container {
    padding: 0 32px;
  }
}
@media screen and (min-width: 1280px) {
  .container {
    padding: 0 100px;
  }
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.loader-wrapper {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  background-color: var(--color-primary-light);
  z-index: 5;
}

.loader {
  position: absolute;
  top: 15%;
  width: 150px;
  height: 150px;
  border: 4px solid var(--color-primary-light);
  box-shadow: 0 -2px 2px rgba(0, 0, 0, 0.2);
  border-left: 6px solid #6ddfca;
  border-bottom: 6px solid #6ddfca;
  border-radius: 50%;
  animation: loader 1000ms linear infinite;
  z-index: 5;
}
@media screen and (min-width: 768px) {
  .loader {
    width: 300px;
    height: 300px;
    border: 6px solid var(--color-primary-light);
    border-left: 8px solid #6ddfca;
    border-bottom: 8px solid #6ddfca;
  }
}
@media screen and (min-width: 1280px) {
  .loader {
    width: 350px;
    height: 350px;
  }
}

.loader:before {
  content: "";
  position: absolute;
  top: 7px;
  left: 7px;
  bottom: 7px;
  right: 7px;
  border: 6px solid var(--color-primary-light);
  box-shadow: 0 -2px 2px rgba(0, 0, 0, 0.2);
  border-bottom: 8px solid #d355be;
  border-radius: 50%;
  animation: loader-after 500ms linear infinite;
}
@media screen and (min-width: 768px) {
  .loader:before {
    top: 10px;
    left: 10px;
    bottom: 10px;
    right: 10px;
    border: 9px solid var(--color-primary-light);
    border-bottom: 12px solid #d355be;
  }
}

.loader:after {
  content: "";
  position: absolute;
  top: 25px;
  left: 25px;
  bottom: 25px;
  right: 25px;
  border: 3px solid var(--color-primary-light);
  box-shadow: 0 -2px 2px rgba(0, 0, 0, 0.2);
  border-bottom: 5px solid #8c23ec;
  border-radius: 50%;
  animation: loader 3500ms linear infinite;
}
@media screen and (min-width: 768px) {
  .loader:after {
    top: 40px;
    left: 40px;
    bottom: 40px;
    right: 40px;
  }
}

@keyframes loader {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes loader-after {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}
.order-post-btn, .rating-post-btn, .btn-rating, .btn-add, .primary-btn {
  display: block;
  font-family: "Inter", sans-serif;
  font-size: 14px;
  line-height: 1.21;
  font-weight: 500;
  padding: 10px 20px;
  border: 1px solid var(--color-accent);
  border-radius: 12px;
  cursor: pointer;
  transition: color 150ms ease, background-color 150ms ease;
}
@media screen and (min-width: 768px) {
  .order-post-btn, .rating-post-btn, .btn-rating, .btn-add, .primary-btn {
    padding: 13px 27px;
    border-radius: 15px;
  }
}

.pag-page, .pag-arrow-next, .pag-arrow-prev, .pag-arrow-last, .pag-arrow-first {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid;
}
@media screen and (min-width: 768px) {
  .pag-page, .pag-arrow-next, .pag-arrow-prev, .pag-arrow-last, .pag-arrow-first {
    width: 44px;
    height: 44px;
  }
}

.pag-arrow-last, .pag-arrow-first {
  display: flex;
}
.pag-arrow-last .svg, .pag-arrow-first .svg {
  flex-shrink: 0;
  margin: -8px;
  width: 24px;
  height: 24px;
  pointer-events: none;
}

.pag-arrow-next .svg, .pag-arrow-prev .svg {
  width: 24px;
  height: 24px;
  pointer-events: none;
}

.primary-btn {
  color: var(--color-secondary-dark);
  background-color: var(--color-primary-light);
}
.primary-btn:hover {
  color: var(--color-primary-light);
  background-color: var(--color-accent);
}

.btn-add {
  color: var(--color-modal-add);
  background-color: var(--color-accent);
}
.btn-add:hover {
  color: var(--color-secondary-dark);
  background-color: transparent;
}

.btn-rating {
  color: var(--color-secondary-dark);
  background-color: var(--color-primary-light);
}
.btn-rating:hover {
  color: var(--color-primary-light);
  background-color: var(--color-accent);
}

.pag-arrow-first {
  background-color: #050505;
}
.pag-arrow-first .svg {
  fill: var(--color-third-light);
}

.pag-arrow-prev {
  display: none;
  background-color: #050505;
}
@media screen and (min-width: 768px) {
  .pag-arrow-prev {
    display: block;
  }
}
.pag-arrow-prev .svg {
  fill: var(--color-third-light);
}

.pag-arrow-last {
  transform: rotate(180deg);
  background-color: var(--color-accent);
  border-color: var(--color-accent);
}
.pag-arrow-last .svg {
  fill: #050505;
}

.pag-arrow-next {
  display: none;
  transform: rotate(180deg);
  background-color: var(--color-accent);
  border-color: var(--color-accent);
}
@media screen and (min-width: 768px) {
  .pag-arrow-next {
    display: block;
  }
}
.pag-arrow-next .svg {
  fill: #050505;
}

.pag-page {
  border-color: var(--color-secondary-light);
  font-family: "Inter", sans-serif;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.22;
  color: var(--color-secondary-dark);
  background-color: transparent;
}
.pag-page.current {
  border-color: var(--color-accent);
  background-color: var(--color-accent);
}
.pag-page.hidden {
  display: none;
}

.rating-post-btn {
  color: var(--color-card-title);
  background-color: var(--color-accent);
  width: 100%;
}
.rating-post-btn:hover {
  color: var(--color-event-back);
}

.order-post-btn {
  color: var(--color-card-title);
  background-color: var(--color-accent);
  width: 100%;
}
.order-post-btn:hover {
  color: var(--color-event-back);
}

.hero-card .card-dish, .hero-card .card-chief {
