}

.master-text {
  font-size: 10px;
  line-height: 1.4;
  font-weight: 500;
  color: var(--color-third-light);
  text-transform: uppercase;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .master-text {
    font-size: 14px;
    line-height: 1.28;
  }
}

.master-text-country {
  color: var(--color-third-lighter);
  font-size: 8px;
  line-height: 1.5;
}
@media screen and (min-width: 768px) {
  .master-text-country {
    font-size: 12px;
    line-height: 1.16;
  }
