.search .input, .options-wrapper {
  font-family: "Inter", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
  color: var(--color-secondary-lighter);
  padding: 14px 18px;
  border: 1px solid var(--color-secondary-lightest);
  border-radius: 15px;
  width: 100%;
  appearance: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .search .input, .options-wrapper {
    font-size: 16px;
    line-height: 1.25;
  }
}

.area-list,
.ingredients-list,
.time-list {
  position: absolute;
  left: 0;
  top: 101%;
  width: 100%;
  height: 180px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 14px 18px;
  background-color: var(--color-primary-light);
  border-radius: 14px;
  box-shadow: 0px 4px 36px 0px rgba(0, 0, 0, 0.02);
  overflow-y: auto;
  z-index: 1;
  cursor: default;
}
.area-list::-webkit-scrollbar,
.ingredients-list::-webkit-scrollbar,
.time-list::-webkit-scrollbar {
  width: 6px;
  position: absolute;
  right: 60px;
}
.area-list::-webkit-scrollbar-thumb,
.ingredients-list::-webkit-scrollbar-thumb,
.time-list::-webkit-scrollbar-thumb {
  background-color: var(--color-secondary-lightest); /* color of the scroll thumb */
  border-radius: 12px; /* roundness of the scroll thumb */
}

.options-wrapper {
  position: relative;
  width: 100%;
  -moz-appearance: none;
  -webkit-appearance: none;
}

.filter {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  gap: 14px;
  margin-bottom: 50px;
  max-width: 686px;
}
@media screen and (min-width: 768px) {
  .filter {
    gap: 16px;
    max-width: 100%;
    margin-bottom: 44px;
  }
}
.filter .arrow {
  position: absolute;
  right: 18px;
  top: 15px;
  width: 18px;
  height: 18px;
  fill: var(--color-secondary-lighter);
  transform: rotate(-90deg);
  pointer-events: none;
}

.filter-label {
  font-size: 12px;
  line-height: 1.16;
  font-weight: 500;
  color: var(--color-secondary-lighter);
  display: block;
  text-align: start;
  margin-bottom: 8px;
}
@media screen and (min-width: 768px) {
  .filter-label {
    font-size: 14px;
    line-height: 1.28;
  }
}

.time {
  width: calc((100% - 14px) / 2);
}
@media screen and (min-width: 768px) {
  .time {
    width: 125px;
  }
}

.area {
  width: calc((100% - 14px) / 2);
}
@media screen and (min-width: 768px) {
  .area {
    width: 141px;
  }
}

.ingredients {
  width: 188px;
}

.search {
  position: relative;
  width: 100%;
}
.search .input {
  padding-left: 48px;
}
@media screen and (min-width: 1280px) {
