}

.filter-btn {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.25;
  text-align: left;
  width: 95%;
  padding: 0;
  color: var(--color-secondary-light);
  background-color: transparent;
  border: none;
  text-overflow: ellipsis; /* enables ellipsis */
  white-space: nowrap;
  overflow: hidden;
  cursor: pointer;
}
.filter-btn:hover {
  color: var(--color-secondary-dark);
}

.reset-filter-btn {
  position: absolute;
  bottom: -30px;
  right: 0;
  display: flex;
  align-items: center;
  gap: 4px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.reset-svg {
  width: 14px;
  height: 14px;
