}

.reset-text {
  color: var(--color-secondary-lighter);
  font-family: "Inter", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  width: 86px;
}

.recipe-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: end;
  max-width: 335px;
  width: 100%;
  height: 335px;
  border-radius: 15px;
  overflow: hidden;
  background: linear-gradient(1deg, rgba(5, 5, 5, 0.6) 4.82%, rgba(5, 5, 5, 0.15) 50%, rgba(255, 255, 255, 0.4) 108.72%);
  padding: 16px;
