}

.nav-logo .src {
  width: 83px;
}
@media screen and (min-width: 768px) {
  .nav-logo .src {
    width: 93px;
  }
}

.logo-light {
  display: var(--display-logo-light);
}

.logo-dark {
  display: var(--display-logo-dark);
}

