  }
}

.popular-content {
  overflow: hidden;
  pointer-events: none;
}

.popular-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
@media screen and (min-width: 768px) {
  .popular-list {
    gap: 32px;
  }
}
.popular-list .card {
  display: flex;
  gap: 16px;
  height: 67px;
  overflow: hidden;
  cursor: pointer;
  transition: transform 150ms ease;
}
.popular-list .card .img {
  flex-shrink: 0;
  width: 64px;
  height: 64px;
  border-radius: 10px;
  pointer-events: none;
}
.popular-list .card .title {
  font-size: 14px;
  line-height: 1.28;
  margin-bottom: 8px;
  text-overflow: ellipsis; /* enables ellipsis */
  white-space: nowrap; /* keeps the text in a single line */
  overflow: hidden;
  pointer-events: none;
}
.popular-list .card .text {
  color: var(--color-primary-dark);
  font-size: 12px;
  line-height: 1.33;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2; /* start showing ellipsis when 3rd line is reached */
  pointer-events: none;
}
@media screen and (min-width: 768px) {
  .popular-list .card .text {
    font-size: 10px;
    line-height: 1.4;
    -webkit-line-clamp: 3; /* start showing ellipsis when 3rd line is reached */
  }
}
.popular-list .card:hover {
  transform: scale(1.1);
  transform-origin: right;
}

